body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body, html {
  height: 100%;
  margin: 0;
}

code {
  font-family: 'Roboto', 'Lato', 'Montserrat', 'Raleway',sans-serif;
}

h2.text-dark {
    text-align: left;
    font-size: 25px;
}

p.card-text {
    text-align: left;
}

div.introduction {
    width: 100%;
    line-height: 2.1;
}

@media (min-width: 600px)  {
div.introduction {
    width: 60%;
    line-height: 2.3;
    }
}


.App {
  text-align: center;
}

.App-intro {
  font-size: large;
}

a { 
  padding-top:10px;
  padding-right: 20px;
  padding-left: 30px;
  color: white;
  font-size: 15px;
  text-decoration: none;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-content: flex-end;
}

h1 {
  text-align: center;
}

div .card.card-body.border-light {
  opacity: 0.9;
  border: 0;
}

div .card-deck {
  text-align: center;
  float: inherit;
}

body {
    background-image: url(/RWeb/static/media/FullSizeRender.39fd994f.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    height: 100%; 
    width: 100%;
}

@media (min-width: 600px)  {
    a, .wrapper {
      font-size: 25px;
  }
}

@media (min-width: 992px)  {
    a, .wrapper {
      font-size: 20px;
      justify-content: flex-end;
  }
}


